$logo-width: 45px;
$logo-height: 26px;

%slider-arrows {
  width: 32px;
  height: 32px;
  background-repeat: no-repeat;
  background-position: 50%;
  border-radius: 50%;
  background-color: #fff;
  border: none;
  outline: none;
  transition: background-color 0.5s;
}

$screen-width-desktop: 1299px;
$screen-width-notebook: 1099px;
$screen-width-small: 559px;

%toggle-span-style {
  display: block;
  position: absolute;
  width: 100%;
  height: 4px;
  background-color: #fff;
  border-radius: 5px;
  transition-duration: 0.3s;
}
